export const toFit = (
  cb: Function,
  { dismissCondition = () => false, triggerCondition = () => true },
) => {
  if (!cb) {
    throw Error('Invalid required arguments')
  }

  let tick = false

  return () => {
    if (tick) {
      return null
    }

    tick = true
    return requestAnimationFrame(() => {
      if (dismissCondition()) {
        tick = false
        return null
      }

      if (triggerCondition()) {
        tick = false
        return cb()
      }
      return null
    })
  }
}
