import { useEffect, useState, useCallback } from 'react'
import qs from 'qs'
import { Category } from '@/types/post'

const useCategory = () => {
  const [category, setCategory] = useState<Category>('ALL')

  const selectCategory = useCallback((selectedCategory: Category) => {
    setCategory(selectedCategory)
    window.history.pushState(
      { selectedCategory },
      '',
      `${window.location.pathname}?${qs.stringify({ category: selectedCategory })}`,
    )
  }, [])

  const changeCategory = useCallback(() => {
    const { category: qsCategory = 'ALL' } = qs.parse(window.location.search, {
      ignoreQueryPrefix: true,
    })
    const target = qsCategory as Category

    setCategory(target)
  }, [])

  useEffect(() => {
    window.addEventListener('popstate', changeCategory)

    return () => {
      window.removeEventListener('popstate', changeCategory)
    }
  }, [])

  useEffect(() => {
    changeCategory()
  }, [])

  return [category, selectCategory] as const
}

export default useCategory
